html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Helvetica, sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

#accordionSitemap .accordion-button::after {
  margin-left: 0.5rem;
}
#accordionSitemap .accordion-button:focus {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.125);
}
#accordionSitemap .accordion-button:not(.collapsed) {
  color: unset;
  background: none;
  box-shadow: none;
}
#accordionSitemap .accordion-button:not(.collapsed)::after {
  background-image: var(--bs-accordion-btn-icon);
}

