@use "../util/" as *;

.nav {
    &--divider {
        & .nav-item {
            &:not(:last-child) {
                & .nav-link {
                    @media (min-width: map-get($breakpoints-up,"xs")) {
                        border-right: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
                    }
                }
            }
        }
    }

    &-link {
        color: $secondary-color;
        &:hover {
            color: $secondary-color-shade-1;
        }
    }
}
